.wrapper {
  width: 100%;
  padding-top: 30px;
  padding-bottom: 30px;
}

.colors {
  width: 80px;
  background-color: #fff;
  margin: 0 auto;
}

.line {
  background-color: #fff;
  display: flex;
}

.line > div {
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: box-shadow 0.1s, transform 0.1s ease;
}

.line > div:hover {
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5);
  transform: perspective(500px) scale(1.3) rotateX(0) rotateY(0);
}

.clear {
  background-color: #fff;
  box-shadow: inset 0 0 1px 1px rgba(0, 0, 0, 0.5);
}

.active {
  border: 4px dotted #fff;
  box-sizing: border-box;
}
