.wrapper {
  margin-top: 20px;
  height: 100px;
  overflow: scroll;
  display: flex;
  flex-wrap: wrap;
}

.color {
  width: 20px;
  height: 20px;
  cursor: pointer;
  transition: box-shadow 0.1s, transform 0.1s ease;
}

.color:hover {
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.5);
  transform: perspective(500px) scale(1.3) rotateX(0) rotateY(0);
}
