.wrapper {
  height: 40px;
  line-height: 40px;
  padding-left: 20px;
  color: #333;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.1s, color 0.1s ease;
}

.wrapper:hover {
  background-color: #3c98e7;
  color: #fff;
}

.wrapper.active {
  background-color: #3c98e7;
  color: #fff;
}
