.label {
  font-size: 14px;
  display: block;
}

.label:not(:last-of-type) {
  margin-bottom: 10px;
}

.label > .title {
  display: flex;
  justify-content: space-between;
}

.label > .title > .note {
  color: red;
}

.label > div {
  margin-bottom: 5px;
}

.content {
  display: flex;
  justify-content: space-between;
}

.buttons {
  position: relative;
  width: 140px;
}

.button {
  position: absolute;
  bottom: 0;
  right: 0;
}

.incorrect {
  color: red;
  transition: color 0.3s;
  animation: fadeOut, 1s;
}

@keyframes fadeOut {
  0% {
    color: red;
  }

  100% {
    color: #141414;
  }
}
