.assets {
  display: flex;
  justify-content: space-between;
  width: 56px;
}

.color {
  width: 20px;
  height: 20px;
  box-sizing: border-box;
  border: 1px dashed #333;
  cursor: pointer;
  position: relative;
}

.color:after {
  content: "\00d7";
  color: #fff;
  font-size: 18px;
  opacity: 0;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: opacity 0.2s ease;
}

.color:hover:after {
  opacity: 100;
}

.color:not(:last-of-type) {
  margin-bottom: 19px;
}

.color.filled {
  border: 1px solid #333;
}

.label {
  font-size: 14px;
  display: block;
}

.label {
  margin-bottom: 5px;
}
